.realisations {
    display: flex;
    flex-direction: column;
}

.project {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;


}

.project .projectContent {
    position: relative;
    width: 500px;
    height: 560px;
    margin: 180px 30px 0px;
    object-fit: contain;

}

.project .projectContent::before {

    content: '';
    position: absolute;
    bottom: -70px;
    width: 100%;
    height: 60px;
    background: radial-gradient(rgba(0, 0, 0, 0.15), transparent, transparent);
    border-radius: 50%;
    transition: 0.5s;

}

.project .projectContent:hover::before {
    opacity: 0.8;
    transform: scale(0.8);
}

.project .projectContent .projetDetail {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;
    clip-path: polygon(0 26%, 50% 0, 100% 26%, 100% 75%, 51% 100%, 0 74%);
    transition: 0.5s;
}

.project .projectContent:hover .projetDetail {
    transform: translateY(-30px);
}

.architecte {
    object-fit: none;
    object-position: 0px 100px;
}

.argentBank {
    width: 550px;
    height: 560px;

}

.project .projectContent .projetDetail .projectText {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    background: rgba(2, 25, 36, 0.5);
    color: #fff;
    opacity: 0;
    transition: 0.5s;
    align-items: center;
    text-align: center;
}

.project .projectContent:hover .projetDetail .projectText {
    opacity: 1;
}

h2 {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 1rem;
}

h3 {
    font-size: 2rem;
    margin-bottom: 0.5rem;
}

.projectCompetence {
    font-size: 1rem;
    margin-bottom: 1rem;
}

.myRealisations-header {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.myRealisations-header h2 {
    position: relative;
    font-size: 30px;
    /* text-transform: uppercase;*/

}

.myRealisations-header hr {
    position: relative;
    width: 70px;
    height: 4px;
    border: none;
    background-color: #1e9bff;
}
.info {
   padding: 0.5rem;
    color: #fff;
    border:2px solid #1e9bff;
    cursor: pointer;
    background-color: #1e9bff;
    border-radius: 10%;
}

@media only screen and (min-width: 852px) {
    .realisations {
        display: flex;
        flex-direction: row;
    }

    .project {
        margin-top: 6rem;
    }

    .project .projectContent {
        margin: 180px 100px 0px;
    }
}